
#show-style{
    position: relative;
    float: left;
    width: 25%;
    height: 100%;
    padding: 1rem 2rem;
    box-sizing: border-box;
    background: #ffffff;
}
.style-content{
    position: relative;
    width: 100%;
    height: 99%;
    overflow: auto;
    box-shadow: 0 4px 12px 0 rgba(0,0,0,0.5);
}
.style-content::-webkit-scrollbar {
    display: none;
}
.style-code{
    margin: 1rem 0;
}



