
#show-menu {
  float: left;
  width: 8%;
  height: 100%;
  background: #ffffff;
}
.menu-cont {
  position: relative;
  width: 90%;
  height: 99%;
  margin: 0 auto;
  padding-top: 1rem;
  box-sizing: border-box;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}
.logo, .file, .download-file, .search, .save-resume {
  width: 100%;
  color: #42b983;
  text-align: center;
  font-size: 1.2rem;
  margin-bottom: 2rem;
}
.logo a {
  color: #42b983;
}
.file .file-des, .search .search-des, .save-resume .save-des {
  font-size: 1.6rem;
}
.download-file {
  position: absolute;
  bottom: 2rem;
}
.logo, .file, .download-file:hover {
  cursor: pointer;
}
.save-bar {
  position: absolute;
  z-index: 9999;
  top: 30px;
  left: 110%;
  width: 50rem;
  height: 150px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 10px;
  background: #fff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}
.save-bar input {
  width: 400px;
  height: 40px;
  border: solid 1px #f2f2f2;
  border-radius: 5px;
  padding: 3px;
}
.save-bar > div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  margin-bottom: 10px;
}
.save-bar > div > label {
  width: 60px;
}
.save-bar > label {
  width: 60px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  margin-left: 60px;
  border-radius: 10px;
  background: #3ca777;
  color: #ffffff;
}
.search-bar {
  position: absolute;
  z-index: 9999;
  top: 100px;
  left: 110%;
  width: 50rem;
  height: 80px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding: 0 10px;
  background: #fff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}
.search-bar > input {
  width: 400px;
  height: 40px;
  border: solid 1px #f2f2f2;
  border-radius: 5px;
  padding: 3px;
}
.search-bar > label {
  width: 60px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  margin-left: 20px;
  border-radius: 10px;
  background: #3ca777;
  color: #ffffff;
}
.search-bar > label:active {
  background: #18A70C;
}
.download-choice {
  position: absolute;
  z-index: 9999;
  bottom: 3.5rem;
  left: 110%;
  width: 35rem;
  text-align: center;
  background: #ffffff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}
.setdpi {
  display: inline-block;
  padding: 1rem;
  font-size: 1.6rem;
  color: #3ca777;
}
.custom {
  display: inline-block;
  width: 10rem;
  font-size: 1.6rem;
  color: #3ca777;
}
.custom input {
  width: 5rem;
  text-align: right;
}
.ok-choice {
  display: inline-block;
  text-align: center;
  border: 1px solid #4cd195;
  border-radius: 6px;
  padding: 1px 8px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}
.ok-choice:hover {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0);
  cursor: pointer;
}



