
#from_box {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.2);
}

/* from-content
-----------------------------------*/
.from-content {
  position: fixed;
  top: 0;
  left: 2%;
  bottom: 0;
  margin: auto auto;
  width: 35%;
  max-width: 450px;
  min-width: 400px;
  height: 85%;
  padding: 1rem;
  box-sizing: border-box;
  background: #ffffff;
}

/* from-content  from-content-item
-----------------------------------*/
.from-content .from-content-item {
  width: 100%;
  height: 100%;
  overflow: auto;
}
.from-content-item::-webkit-scrollbar {
  display: none;
}
.from-content .item {
  color: #ffffff;
  font-size: 2rem;
  margin-bottom: 1.4rem;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}
.from-content .item-head {
  width: 100%;
  background: #42b983;
  padding: 0.8rem 0;
  text-indent: 1.4rem;
}
.item-head .item-head-time {
  display: inline-block;
  text-align: right;
  font-size: 1.6rem;
  float: right;
  margin-top: 4px;
  margin-right: 1rem;
}
.item-head-time input {
  background: #42b983;
  border-bottom: 2px solid #ffffff;
  text-indent: 1rem;
  color: #ffffff;
}
.from-content .item-content {
  color: #313131;
  font-size: 1.6rem;
  margin: 1rem 1rem;
  line-height: 1.5;
}
.from-content .item-footer {
  width: 100%;
  background: #42b983;
  text-align: right;
}
.from-content .inp-short {
  width: 50%;
  float: left;
}
.inp-short input[type='text'] {
  border-bottom: 2px solid #42b983;
  text-indent: 1rem;
}
.from-content .inp-long {
  width: 100%;
}
.from-content .inp-long-more {
  line-height: 1.6;
}
.inp-long input[type='text'] {
  border-bottom: 2px solid #42b983;
  text-indent: 1rem;
  width: 100%;
}
.from-content .last-item {
  margin-bottom: 4.5rem;
}
.from-content .xs {
  position: absolute;
  top: 0;
  right: 0;
  width: 0;
  height: 0;
  border-top: 4rem solid #3ca777;
  border-right: 4rem solid #3ca777;
  border-bottom: 4rem solid transparent;
  border-left: 4rem solid transparent;
}
.from-content .xs2 {
  position: absolute;
  top: 0;
  right: 0;
  width: 0;
  height: 0;
  border-top: 2rem solid #ffffff;
  border-right: 2rem solid #ffffff;
  border-bottom: 2rem solid transparent;
  border-left: 2rem solid transparent;
}

/* from-content from-box-footer
-----------------------------------*/
.from-content .from-box-footer {
  position: absolute;
  bottom: 8px;
  left: 5px;
  width: 100%;
  height: 4rem;
  background: #ebebeb;
  box-shadow: -4px 9px 8px 0 rgba(0, 0, 0, 0.2);
}
.from-box-footer .butt {
  display: inline-block;
  border-radius: 0.4rem;
  border: 1px solid #3ead7b;
  font-size: 2rem;
  color: #3ca777;
  float: right;
  padding: 0.2rem;
  margin: 0.5rem 1rem 0 0;
}
.from-box-footer .butt:hover {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  cursor: pointer;
}



